// usually would come directly from a feed
window.pilets = [
  () => import("./pilets/layout"),
  () => import("./pilets/sample"),
  () => import("./pilets/extension")
];

// call would certainly be similar, but the pilets would come from a feed
window.initializePiral(() =>
  Promise.resolve(
    window.pilets.map((_, i) => ({
      content: `
      // This magic is only required in the codesandbox env.
      window.deps = {
        react: require('react'),
        reactRouterDom: require('react-router-dom'),
      };
      module.exports = window.pilets[${i}]();`,
      name: `pilet-${i}`,
      version: "1.0.0",
      hash: ""
    }))
  )
);
